:root{
    --path1-gradient: linear-gradient()
}

.app-container{
    .app-routing-elements{
        display: block;
        position: relative;
        margin: auto;
        z-index: 1;
        max-width: 500px;
    }
    .app-info-container{
        position: fixed;
        z-index: 1;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        text-align: center;
        margin-bottom: 35px;
        opacity: 0.6;
        *{
            margin-bottom: 3px;
        }
    }
    @keyframes animateReveal{
        from{
            pointer-events: none;
            opacity: 0;
        }
        to{
            pointer-events: initial;
            opacity: 1;
        }
    }
    @keyframes animatePath1{
        from{
            transform: translate(-200% , -300%)
        }
        to{
            transform: translate(-120%, -300%);
        }
    }
    @keyframes animatePath2{
        from{
            transform: translate(-360%, -400%);
        }
        to{
            transform: translate(-400%, -400%);
        }
    }
}